import Link from 'next/link'
import fetch from 'isomorphic-unfetch'
import urlname from '../components/urlname.js'
import { connect } from "react-redux";
import Page from "./page.js"
import { updateGame, updateName } from "../redux/actions.js";

class LoginPage extends React.Component{
	constructor(props){
		super(props);

		this.state = {
			name: ""
		}
		this.changeName = this.changeName.bind(this);
		this.startGame = this.startGame.bind(this);
	}

	changeName(event){
		this.props.dispatch(updateName(event.currentTarget.value));
	}

	startGame(event){
		fetch(urlname + "").then(response => response.text()).then(data => {
			

		})
	}

	render(){
		return(
			<div>
				<h3>Enter your name:</h3>
				<form onSubmit={e => {e.preventDefault();}}>
					<input type='text' onChange={this.changeName}/>
				</form>
				<Link  href={{ pathname: '/game', query: { name: this.state.ingredtextlist.toString() } }}>
					<button onClick={this.startGame}>Start game!</button>
				</Link>
			</div>
		)
	}
}

export default Page(connect(state=>state)(LoginPage))


